<script lang="ts">import { fade } from "svelte/transition";
import Info from "./Info.svelte";
import { cleanText, copyText } from "./utils.svelte";
let info = false;
let diacsCheck = true;
let extrasCheck = false;
let origTextInput = "";
let cleanTextField;
$: cleanTextOutput = cleanText(origTextInput, diacsCheck, extrasCheck);
</script>

<style>
  main {
    max-width: 72ch;
    padding: 2ch;
    margin: auto;
    font-family: "Noto Serif", serif;
  }
  button {
    margin-bottom: 1em;
    background-color: #007bff;
    color: #fff;
    border: 1px solid transparent;
    border-radius: 0.25rem;
    border-color: #007bff;
    transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out,
      border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
  }
  button:hover,
  button:focus {
    background-color: #0069d9;
    border-color: #0062cc;
  }
  button:focus {
    box-shadow: 0 0 0 0.2rem rgba(38, 143, 255, 0.5);
  }
  code {
    font-size: 150%;
    color: #e83e8c;
    margin-right: 1em;
  }
  h1 {
    margin-top: -0.3em;
  }
  textarea {
    width: 100%;
  }
  .checkboxLabel {
    display: inline-block;
    margin-right: 1em;
  }
  .count {
    margin-bottom: 0.4em;
  }
  .textareaLabel {
    margin-bottom: 0.4em;
  }
  #checkboxes {
    display: flex;
    flex-flow: row wrap;
    margin-bottom: 0.6em;
  }
  #counts {
    display: flex;
    flex-flow: row wrap;
  }
  #infoButton {
    display: flex;
    justify-content: flex-end;
    font-size: small;
  }
</style>

<svelte:head>

  <meta
    name="description"
    content="Clean text to prepare for pasting into a document." />

  <link
    href="https://fonts.googleapis.com/css2?family=Noto+Serif:ital@0;1&display=swap"
    rel="stylesheet" />

  <link href="#" rel="icon" />

  <title>Clean text for pasting</title>

</svelte:head>

<main>

  <div id="infoButton">
    <button on:click={() => (info = !info)}>
      {#if !info}Show info{:else}Return to utility{/if}
    </button>
  </div>

  <h1>Clean text for pasting</h1>

  {#if !info}
    <div in:fade>

      <div id="checkboxes">
        <div>
          <input type="checkbox" id="diacsCheck" bind:checked={diacsCheck} />
          <label for="diacsCheck" class="checkboxLabel">Fix ṡ, ż</label>
        </div>

        <div>
          <input type="checkbox" id="extrasCheck" bind:checked={extrasCheck} />
          <label for="extrasCheck" class="checkboxLabel">Extras</label>
        </div>
      </div>

      <div>
        <label for="origTextInput" class="textareaLabel">Input:</label>
        <textarea bind:value={origTextInput} id="origTextInput" rows="4" />
      </div>

      <div>
        <button on:click={() => (origTextInput = '')}>Clear</button>
      </div>

      <div>
        <label for="cleanTextOutput" class="textareaLabel">Output:</label>
        <textarea
          bind:this={cleanTextField}
          bind:value={cleanTextOutput}
          readonly
          autocomplete="off"
          id="cleanTextOutput"
          rows="4" />
      </div>

      <div>
        <button on:click={() => copyText(cleanTextOutput, cleanTextField)}>
          Copy
        </button>
      </div>

      <div id="counts">
        <div class="count">
          Characters:
          <code>{String(cleanTextOutput.length)}</code>
        </div>

        <div class="count">
          Words:
          <code>
            {cleanTextOutput.length === 0 ? '0' : String(cleanTextOutput
                    .replace(/\s{2,}/gm, ' ')
                    .split(/\s/gm).length)}
          </code>
        </div>

        <div class="count">
          Paragraphs:
          <code>
            {cleanTextOutput.length === 0 ? '0' : String(cleanTextOutput.split('\n\n').length)}
          </code>
        </div>
      </div>

    </div>
  {:else}
    <Info />
  {/if}

</main>
